import { createBrowserNavigation } from "navi";
import React, { Suspense } from "react";
import PropTypes from "prop-types";
import { Router, View } from "react-navi";
import HelmetProvider from "react-navi-helmet-async";
import "./index.module.css";
import routes from "./routes";
// import * as serviceWorker from "./serviceWorker";

class Blog extends React.Component {
  constructor(props) {
    super(props);

    const { history, basename } = this.props;

    this.state = {
      navigation: createBrowserNavigation({
        history,
        basename,
        routes
      })
    };
  }

  componentDidMount() {
    // Wait until the navigation has loaded the page's content, or failed to do
    // so. If you want to load other data in parallel while the initial page is
    // loading, make sure to start loading before this line.
    const getRoute = async () => {
      await this.state.navigation.getRoute();

      this.setState( state => ({
        navigation: state.navigation
      }));
    };

    getRoute();
  }

  render() {
    const { navigation } = this.state;
    // Start react, passing in the current navigation state and
    // rendering the top-level view.
    return (
      <HelmetProvider>
        <Router navigation={navigation}>
          <Suspense fallback={null}>
            <View />
          </Suspense>
        </Router>
      </HelmetProvider>
    );
  }
};

Blog.propTypes = {
  history: PropTypes.instanceOf(Object),
  basename: PropTypes.string
};

Blog.defaultProps = {
  history: {},
  basename: "/"
};

export default Blog;
