import styled, { keyframes } from 'styled-components';
import Theme from 'theme/theme';

const translateUp = keyframes`
  from {
    transform: translateY(0)
  }
  to {
    transform: translateY(-12px);
  }
`;

const translateDown = keyframes`
from {
  transform: translateY(-12px)
}
to {
  transform: translateY(0);
}
`;

const expandLineWidth = keyframes`
  from {
    width: 0
  }
  to {
    width: 60px;
  }
`;

const shrinkLineWidth = keyframes`
  from {
    width: 60px;
  }
  to {
    width: 0;
  }
`;

export const Project = styled.div`
  display: flex;
  flex-direction: row;
  align-items: start;
  margin-top: 16px;

  &:last-of-type {
    margin-bottom: 36px;
  }

  .thumbnail {
    min-width: ${props => (['xs'].includes(props.screen) ? '120px' : '150px')};
    min-height: 6rem;
    width: ${props => (['xs'].includes(props.screen) ? '120px' : '150px')};
    height: 6rem;
    margin-right: 16px;
    border: 1px solid;
    border-color: rgba(255, 255, 255, 0.4);
    padding: 8px;
    font-weight: bold;
    font-size: 0.8rem;
    color: ${Theme.primary};
    display: flex;
    align-items: flex-end;
    position: relative;
    cursor: pointer;
    transition: border-color 275ms ease-out, background-color 275ms ease-in;
    overflow: hidden;

    span {
      position: relative;
      animation: ${translateDown} 1s cubic-bezier(0.19, 1, 0.22, 1);
      animation-fill-mode: forwards;

      &:before {
        content: '';
        position: absolute;
        width: 0;
        height: 1px;
        background-color: ${Theme.accent};
        top: -10px;
        left: 0;
        animation: ${shrinkLineWidth} 1s cubic-bezier(0.19, 1, 0.22, 1);
        animation-fill-mode: forwards;
      }
    }

    &:after {
      content: '';
      position: absolute;
      bottom: -1px;
      left: 5px;
      width: 60px;
      height: 1px;
      background-color: ${Theme.accent};
    }

    &:hover {
      border-color: rgba(255, 255, 255, 1);
      background-color: rgba(255, 255, 255, 1);
      color: rgba(0, 0, 0, 1);
      transition: all 275ms ease-in;

      span {
        animation: ${translateUp} 1s cubic-bezier(0.19, 1, 0.22, 1);
        animation-fill-mode: forwards;

        &:before {
          animation-delay: 750ms;
          animation: ${expandLineWidth} 2s cubic-bezier(0.19, 1, 0.22, 1);
          animation-fill-mode: forwards;
        }
      }
    }
  }

  .description {
    line-height: 1.2rem;
    font-size: 1.1rem;
  }
`;

export const ProjectType = styled.h2`
  font-size: 5rem !important;
  color: ${props => (props.active ? Theme.accent : 'rgba(255, 255, 255, 0.7)')};
  transition: color 275ms ease-out;

  &:last-of-type {
    margin-bottom: 0;
  }

  &:hover {
    cursor: pointer;
    color: ${Theme.accent};
    transition: color 275ms ease-in;
  }
`;
